<template>
  <div class="order">
    <div class="order__price">实付金额&nbsp;&yen;<span class="order__price__number">{{ price }}</span></div>
    <div class="order__btn" @click="handleSubmitOrder">提交订单</div>
  </div>
  <div class="mask" v-if="tipsConfirm" @click="handleCancelOrder">
    <div class="tips" @click.stop>
      <div class="tips__title">确认订单信息</div>
      <div class="tips__desc">请尽快完成支付，否则将被取消</div>
      <div class="tips__btns">
        <div class="tips__btn" @click="handleCancelOrder">取消订单</div>
        <div class="tips__btn" @click="handleConfirmOrder">确认支付</div>
      </div>
    </div>
  </div>
  <Toast v-if="toastInfo.show" :message="toastInfo.message"/>
</template>

<script>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { post } from '@/utils/request'
import Toast, { useToastEffect } from '@/components/Toast.vue'
import { getShopId, getShopName, getProductList, useCommonCartEffect } from '@/effects/cartEffects'

export default {
  name: 'Order',
  components: { Toast },
  setup (props, { emit }) {
    const router = useRouter()
    const shopId = getShopId()
    const productList = getProductList()
    const tipsConfirm = ref(false)

    let price = 0
    for (const key in productList) {
      const product = productList[key]
      product.check && (price += product.price * product.count)
    }

    const {
      toastInfo,
      showToast
    } = useToastEffect()

    const handleSubmitOrder = () => {
      tipsConfirm.value = true
    }
    const handleCancelOrder = () => {
      tipsConfirm.value = false
    }
    const { cleanCartProducts } = useCommonCartEffect()
    const handleConfirmOrder = async () => {
      const shopName = getShopName()
      const products = []
      for (const key in productList) {
        const product = productList[key]
        products.push({
          id: Number(product._id),
          num: product.count
        })
      }
      try {
        const result = await post('/api/order', {
          addressId: 1,
          shopId,
          shopName,
          isCanceled: false,
          products
        })
        if (result.errno !== 0) return showToast(result.desc)
        cleanCartProducts(shopId)
        router.push('/')
      } catch (e) {
        showToast(e.message)
      }
    }
    return {
      price: price.toFixed(2),
      toastInfo,
      tipsConfirm,
      handleSubmitOrder,
      handleCancelOrder,
      handleConfirmOrder
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/variables";

.order {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  line-height: .49rem;
  display: flex;

  &__price {
    flex: 1;
    font-size: .14rem;
    color: $content-fontColor;
    text-indent: .24rem;

    &__number {
      font-size: .16rem;
      color: #151515;
      font-weight: bold;
    }
  }

  &__btn {
    width: .98rem;
    background: #4FB0F9;
    color: $bgColor;
    font-size: .14rem;
    text-align: center;
  }
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: $content-notice-fontColor;
}

.tips {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 3.01rem;
  height: 1.57rem;
  background: #FFFFFF;
  border-radius: .04rem;
  padding: .24rem;
  box-sizing: border-box;
  text-align: center;

  &__title {
    line-height: .25rem;
    font-size: .18rem;
    color: $content-fontColor;
    margin-bottom: .08rem;
    font-weight: bold;
  }

  &__desc {
    line-height: .2rem;
    font-size: .14rem;
    color: $medium-fontColor;
    margin-bottom: .24rem;
  }

  &__btns {
    display: flex;
    justify-content: center;
  }

  &__btn {
    border-radius: .16rem;
    font-size: .14rem;

    &:first-child {
      width: .78rem;
      line-height: .3rem;
      border: .01rem solid #4FB0F9;
      color: $btn-bgColor;
      margin-right: .24rem;
    }

    &:last-child {
      width: .8rem;
      line-height: .32rem;
      background: #4FB0F9;
      color: $bgColor;
    }
  }
}
</style>
